import React, { Component } from 'react';
import '../Overall.css';

class App extends Component {
  render() {
    return (
        <a href={this.props.url} className="button iproject-button">
          <div className="iproject-container">
            <img src={this.props.picture} alt={this.props.description} width="250px" height="250px"/>
            <div className="iproject-overlay">
              <p className="iproject-title-text">{this.props.name}</p>
              <p className="text">{this.props.languages}</p>
            </div>
          </div>
        </a>
    );
  }
}

export default App;
